<template>
<div class="home-container">
   <van-nav-bar fixed
    title="今日新闻"
   />
 <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  @load="onLoad">
   <!-- 导入，注册，并使用ArticleInfo -->
           <ArticleInfo v-for="item in artlist"
           :key="item.id"
           :title="item.title"
           :author="item.aut_name"
           :cmtCount="item.comm_count"
           :pudData="item.pubdate"
           :cover="item.cover"
           ></ArticleInfo>
    </van-list>
  </van-pull-refresh>
</div>
</template>

<script>

import request from '@/utils/request.js'
import ArticleInfo from '@/components/Article/ArticleInfo.vue'

export default {
  name: 'Home',
  data () {
    return {
      // 页码值
      page: 1,
      // 每页显示多少条数据
      limit: 10,
      artlist: [],
      loading: true,
      finished: false,
      isLoading: false
    }
  },
  created () {
    this.initArticleList()
  },
  methods: {
    // 封装获取文章列表数据的方法
    async initArticleList (isRefresh) {
      const { data: res } = await request.get('/articles', {
        params: {
          _page: this.page,
          _limit: this.limit
        }
      })
      if (isRefresh) {
        // 证明是下拉刷新,新数据在前，旧数据在后
        this.artlist = [...res, ...this.artlist]
        this.isLoading = false
      } else {
        this.artlist = [...this.artlist, ...res]
        this.loading = false
      }
      // this.artlist = [...this.artlist, ...res]
      // this.loading = false
      if (res.length === 0) {
        this.finished = true
      }
    },
    onLoad () {
      // console.log('触发了load事件')
      // 1.让页码值+1
      this.page++
      // 2.重新获取接口请求数据
      this.initArticleList()
    },
    onRefresh () {
      // console.log('触发了下拉刷')
      // 1.让页码值加1
      this.page++
      // 2.重新获取接口请求数据
      this.initArticleList(true)
    }
  },
  components: {
    ArticleInfo
  }
}
</script>

